import React,{Component ,Fragment} from 'react';
import './atom.css';
export class HomeMobile extends Component{
    render(){
        return(
            <Fragment>
                <div className="home-mobile">
                    <img src="data:image/png;base64," alt="mobile home" />
                </div>
            </Fragment>
        )
    }
}

